<script>
import $ from 'jquery';

export default {
  data() {
    return {};
  },
  methods: {
    // general function
    goPage(page) {
      this.$router.push(`/${page}`);
    },
    goTop() {
      $('html').animate({ scrollTop: '0' }, 500);
    },
    toggle_arrow(elementStr) {
      const parent = document.querySelector(`.${elementStr}__list`);
      const children = document.querySelectorAll(`.${elementStr}__list__item`);
      const leftArrow = document.querySelector(`.${elementStr}__leftArrow`);
      const rightArrow = document.querySelector(`.${elementStr}__rightArrow`);

      const parentScrollLeft = $(parent).scrollLeft();
      const parentWidth = $(parent).width();
      let childrenWidthSum = 0;
      children.forEach((child) => {
        childrenWidthSum += $(child).outerWidth(true);
      });

      if (parentWidth < childrenWidthSum) {
        // 最左邊 parentScrollLeft = 0
        // 最右邊 parentScrollLeft + parentWidth >= childrenWidthSum
        if (parentScrollLeft !== 0) {
          $(leftArrow).fadeIn('fast');
        } else {
          $(leftArrow).fadeOut('fast');
        }
        // childrenWidthSum可能會有小數點，可是parentScrollLeft + parentWidth不會有小數點
        // 所以用childrenWidthSum - 1做標準，不然也可以用childrenWidthSum無條件捨去到整數
        if (parentScrollLeft + parentWidth < childrenWidthSum - 1) {
          $(rightArrow).fadeIn('fast');
        } else {
          $(rightArrow).fadeOut('fast');
        }
      } else {
        $(leftArrow).fadeOut('fast');
        $(rightArrow).fadeOut('fast');
      }
    },
    scroll_horizontally(elementStr, direction) {
      const vm = this;
      const element = document.querySelector(`.${elementStr}__list`);
      if (direction > 0) {
        $(element).stop().animate({
          scrollLeft: '+=50px',
        }, () => {
          vm.toggle_arrow(elementStr);
        });
      } else {
        $(element).stop().animate({
          scrollLeft: '-=50px',
        }, () => {
          vm.toggle_arrow(elementStr);
        });
      }
    },
    activate_wheeling_list(elementStr) {
      const vm = this;
      $(window).bind('mousewheel', (e) => {
        // e.originalEvent.deltaY 負的往上
        if (e.originalEvent.deltaY > 0) {
          vm.scroll_horizontally(elementStr, 1);
        } else {
          vm.scroll_horizontally(elementStr, -1);
        }
      });
    },
    deactivate_wheeling_list() {
      $(window).unbind('mousewheel');
    },

    // specific element function
    toggle_headerNav() {
      const front = document.querySelector('#front');
      $(front).toggleClass('rwdM--headerNav--open rwdM--category--shadowed');
    },
    change_headerNavListItem_active(itemIndex) {
      const itemGroup = document.querySelectorAll('.header__nav__list__item');
      itemGroup.forEach((item) => {
        $(item).removeClass('active');
      });
      $(itemGroup[itemIndex]).addClass('active');
    },

    // initialize window
    initialize_window() {
      const vm = this;
      const front = document.querySelector('#front');
      const header = document.querySelector('.header');
      const banner = document.querySelector('.banner');
      vm.toggle_arrow('header__nav');
      vm.toggle_arrow('category');

      $(window).resize(() => {
        vm.toggle_arrow('header__nav');
        vm.toggle_arrow('category');
      });

      $(window).scroll(() => {
      // rwdM--category--shadowed
      // rwdL--header--shadowed
      // rwdL--category--fixed
        const windowScrollY = window.scrollY;
        const headerHeight = $(header).height();
        const bannerHeight = $(banner).height();
        if (windowScrollY >= 1) {
          $(front).addClass('rwdM--category--shadowed');
        } else if (headerHeight !== 90) {
          $(front).removeClass('rwdM--category--shadowed');
        }

        if (windowScrollY >= 1 && windowScrollY < bannerHeight) {
          $(front).addClass('rwdL--header--shadowed');
        } else {
          $(front).removeClass('rwdL--header--shadowed');
        }

        if (windowScrollY >= bannerHeight) {
          $(front).addClass('rwdL--category--fixed');
        } else {
          $(front).removeClass('rwdL--category--fixed');
        }
      });
    },

  },
  created() {},
  mounted() {
    this.initialize_window();
  },
};
</script>
